<template>

    <div class="container-fluid">
        <Navbar />

        <div class="container">
            <div class="row mt-3">
                <div class="col-md-12">
                    <h1>{{ resep.judul_resep }}</h1>
                    <h5>{{ resep.penjelasan }}</h5>
                </div>
            </div>

            <table class="table table-borderless mt-3">
                <tbody>
                    <tr>
                    <th scope="row">Bahan-bahan</th>
                    <td>{{ resep.bahan }}</td>
                    </tr>
                    <tr>
                    <th scope="row">Cara Membuat</th>
                    <td>{{ resep.langkah }}</td>
                    </tr>
                </tbody>
            </table>

            
        </div>

    </div>

</template>

<script>
import Navbar from "@/components/Navbar.vue"
import axios from "axios";

export default {
    name: 'ResepDetail',
    components: {
        Navbar
    },
    data() {
        return {
            resep: {}
            }
    },
    methods: {
        setResep(data) {
            this.resep = data
        }
    },
    mounted() {
        axios
        .get("resep/"+this.$route.params.id_resep)
        .then((response) => this.setResep(response.data))
        .catch((error) => console.log(error));
    } 
}
</script>

