<template>

    <section id="aneka" class="aneka">
      <div class="container-fluid">
        <div class="container">
          <div class="row text-start mb-3 mt-3">
            <div class="col">
              <h2>Aneka Resep Khas Kota</h2>
            </div>
          </div>
          <div class="row text-center mb-3 my-3">
            <div class="col-lg-3 col-md-6 mt-4" v-for="resep in reseps" :key="resep.id_resep">
              <div class="card">
                <img
                  src=""
                  class="card-img-top"
                  alt="error image"
                />
                <div class="card-body">
                  <h5 class="card-title">
                    {{ resep.judul_resep }}
                  </h5>
                  <p class="card-text">
                    {{ resep.penjelasan }}
                  </p>
                  <router-link class="btn btn-outline-dark" style="font-size: medium;" :to="'/resep/'+resep.id_resep">Yuk Coba</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

</template>

<script>
import axios from "axios";

export default {
  name: "Resep",
  data() {
    return {
      reseps: [],
    };
  },
  mounted() {
    this.getReseps();
  },
  methods: {
    async getReseps() {
      try {
        const response = await axios.get('resep');
        this.reseps = response.data;
      } catch (error) {
        console.error("Terjadi kesalahan saat mengambil data:", error.message);
      }
    },
  },
};
</script>

